import {api} from '/js/api';
import {Store} from '/js/util';
import {Languages} from '../../js/util';

api({
	'signIn!post': 'userscore/SignActivityOrMeeting',
});

document.getElementById('btnTest').addEventListener('click', function () {

	let param: any = {
		sourcescore: 1000,
		type: 0, //签到
	};


	/*test code*/
	let userName = Store.get('userName');
	if (userName === 'buyer') {
		param.userId = 1;
	}
	else {
		param.userId = 3;
	}
	/*end test code*/


	api.signIn(param, data => {
		if(data === 'ok')
			mui.alert(Languages.package.signSuccess , Languages.package.tip , Languages.package.okb);
		else{
			mui.alert(Languages.package.signNotOpen , Languages.package.tip , Languages.package.okb);
		}
	});

});